<template>
  <div class="univariate-monitor-result">
    <el-card>
      <div slot="header">
        <span class="card-name">变量监控图</span>
      </div>
      <univariate-monitor-chart ref="univariateMonitorChart"></univariate-monitor-chart>
    </el-card>
  </div>
</template>

<script>
import univariateMonitorChart from '@/components/univariate-monitor-chart';
import {queryAnalysisDetail} from '@/api/analysis.js';

export default {
  name: 'univariate-monitor-result',
  components: {
    univariateMonitorChart
  },
  props: {
    recordId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      initSample: [],
      initVar: [],
      decisionDates: [],
      moduleName: '',
      varKey: ''
    };
  },
  created() {
    if (this.recordId) {
      this.getAnalysisResult();
    }
  },
  methods: {
    getAnalysisResult() {
      queryAnalysisDetail(this.recordId)
        .then(result => {
          if (result.result == null) {
            this.$message.warning('此历史记录不存在');
            return;
          }
          this.dealWithParams(result.result);
        })
        .catch(err => console.log(err));
    },
    dealWithParams(result) {
      this.filename = result.name;
      this.filesaveid = result.id;
      const params = JSON.parse(result.result);
      this.initSample = params.initSample;
      this.decisionDates = [params.startTime, params.endTime];
      this.varKey = params.variablekey;
      this.moduleName = params.modulename;
      this.params = params;

      this.$nextTick(() => {
        this.$refs.univariateMonitorChart.$emit(
          'drawUnivariateMonitorChart',
          this.moduleName,
          this.varKey,
          this.decisionDates[0],
          this.decisionDates[1]
        );
      });
    }
  },
  beforeDestory() {
    this.$store.commit('UPDATE_MONITORPAGE', {
      key: 'univariate',
      value: 'form'
    });
  }
};
</script>

<style scoped>
.univariate-monitor-result {
  margin-bottom: 60px;
}
.el-card {
  margin: 20px 15px;
}
</style>
