export function getBarOption(title, datalist) {
  return {
    title: {
      text: title,
      left: 'center',
      textStyle: {
        fontWeight: 'bold',
        fontSize: 16
      }
    },
    color: ['#3398DB'],
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      },
      formatter: '{a} :<br />{b} : {c}',
      padding: [5, 12]
    },
    grid: {
      left: '3%',
      right: 80,
      bottom: '3%',
      containLabel: true
    },
    xAxis: [
      {
        type: 'category',
        data: datalist.map(({key}) => key),
        axisTick: {
          show: false
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisTick: {
          show: false
        }
      }
    ],
    series: [
      {
        name: '分析结果',
        type: 'bar',
        label: {
          show: false,
          position: 'top'
        },
        data: datalist.map(({value}) => value)
      }
    ]
  };
}
