import {getBarOption} from '@/assets/scripts/getBarOption.js';

const mixins = {
  methods: {
    getTitle(i) {
      let data = this.resultallList[i].postdata;
      if (data.loanType === 'BEFORE_LOAN') {
        let subTitle = '';
        if (data.resultValueType === 'FIELD_AVG') {
          let theOne = this.varList.find(item => item.value === data.resultField) || {};
          subTitle = `平均值_${theOne.label}`;
        } else {
          subTitle = `计数`;
        }
        return `贷前_${subTitle}`;
      } else if (data.loanType === 'AFTER_LOAN') {
        let value = `${data.afterLoanKPI}-${data.overdueDays}`;
        let theOne = this.overduelist.find(item => item.value === value) || {};
        return `贷后_${theOne.text}_${data.resultValueType === 'COUNT' ? '人数' : '金额'}_${
          data.resultNeedPercent ? '逾期率' : '逾期量'
        }`;
      }
      return '';
    },
    initFields(resultallList) {
      if (resultallList.length === 0) {
        return;
      }
      const paramsaved = resultallList[0].postdata;
      if (!paramsaved.indexName) {
        return;
      }
      this.rcProductName = paramsaved.rcProductName;
      this.moduleType = String(paramsaved.moduleType);
      this.samplename = Array.isArray(paramsaved.indexName) ? paramsaved.indexName[0] : paramsaved.indexName;
      if (paramsaved.createTimeStart && paramsaved.createTimeEnd) {
        this.decisionDates = [paramsaved.createTimeStart, paramsaved.createTimeEnd];
      }
      if (paramsaved.loanTimeStart && paramsaved.loanTimeEnd) {
        this.loanDates = [paramsaved.loanTimeStart, paramsaved.loanTimeEnd];
      }
      for (let item of resultallList) {
        const data = item.postdata;
        if (!this.indicator.includes(data.loanType)) {
          this.indicator.push(data.loanType);
        }
        if (data.loanType === 'BEFORE_LOAN') {
          // 贷前
          this.showloanbefore = true;
          if (data.resultValueType === 'COUNT') {
            this.beforeway = 'count';
          }
          if (data.resultValueType === 'FIELD_AVG') {
            this.beforeway = 'avg';
            this.avgvar = data.resultField;
            paramsaved.initAvgvar = data.initAvgvar;
          }
          //this.ispercent = data.resultNeedPercent;
          this.ispercent = item.ispercent;
        } else if (data.loanType === 'AFTER_LOAN') {
          // 贷后
          this.showloanafter = true;
          this.overdue.push(`${data.afterLoanKPI}-${data.overdueDays}`);
          if (data.resultValueType === 'COUNT') {
            this.aftertype = 'peoples';
          }
          if (data.resultValueType === 'FIELD_SUM') {
            this.aftertype = 'amount';
          }
          if (data.resultNeedPercent === true) {
            this.aftercount = 'rate';
          } else {
            this.aftercount = 'count';
          }
        }
      }
      return paramsaved;
    },
    drawBar(data, i = 0) {
      console.log('=====开始画图=====');
      if (!data.length) {
        return;
      }
      let datalist = data.map(function({key, value}) {
        return {
          key: key,
          value: parseFloat(value)
        };
      });
      let elem = this.$el.querySelector(`#resultbar${i}`);
      if (!elem) {
        return;
      }
      this.mychart = echarts.init(elem);
      let theOne = this.varList.find(item => item.value === this.varselectlist[0].fieldName) || {};
      let title = `${theOne.label}-${this.getTitle(i)}统计图`;
      let option = getBarOption(title, datalist);
      option.xAxis[0].name = '变量取值';
      let reqData = this.resultallList[i].postdata;
      let name = '';
      if (reqData.loanType === 'BEFORE_LOAN') {
        name = reqData.resultNeedPercent ? '百分比（%）' : '计数';
      } else if (reqData.loanType === 'AFTER_LOAN') {
        name = reqData.resultNeedPercent ? '逾期率（%）' : '逾期量';
      }
      option.yAxis[0].name = name;
      if (option.series[0].data.every(v => isNaN(v))) {
        console.log('======有非数字值存在=====');
        this.resultallList[i].showbar = false;
        return;
      }
      if (option.series[0].data.length < 10) {
        option.series[0].barWidth = 20;
      }
      if (option.series[0].data.length > 30) {
        option.series[0].barWidth = 10;
      }
      this.mychart.setOption(option);
    }
  }
};

export default mixins;
