<script>
import qs from 'qs';
import dayjs from 'dayjs';
import datepicker from '@/components/datepicker';
import decisionMonitorChart from '@/components/decision-monitor-chart';
import analysisRecords from '@/components/analysis-records';
import variationMonitorDecision from '@/components/variation-monitor-decision.vue';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';
import {getMessageList, updateMessageStatus} from '@/api/message.js';
import {getProMonDataFromYday} from '@/api/monitor.js';

const envObj = {
  OverdueData: '逾期指数',
  CheatData: '欺诈指数',
  ApplyData: '申请指数',
  ActiveData: '热度指数'
};

export default {
  name: 'home',
  components: {
    datepicker,
    decisionMonitorChart,
    analysisRecords,
    variationMonitorDecision
  },
  data() {
    return {
      totalList: [
        {
          name: '昨日申请量',
          url: '/img/total-3.png',
          isProgress: false,
          value: 0,
          type: 'count',
          styleObj: {background: '#3B4D5B'}
        },
        {
          name: '昨日通过率',
          url: '/img/total-2.png',
          isProgress: true,
          value: 0,
          type: 'resultrate',
          progressColor: '#E4A4EC',
          styleObj: {background: '#58A4B1'}
        },
        {
          name: '昨日通过量',
          url: '/img/total-3.png',
          isProgress: false,
          value: 0,
          type: 'passingCount',
          progressColor: '#E78787',
          styleObj: {background: '#9BAEC5'}
        }
      ],
      moduleType: '1',
      rcProductName: '卡卡贷',
      productList: [],
      headerStyle: {
        color: '#409eff'
      },
      recordList: [],
      msgListArr: [],
      envDates: [
        dayjs()
          .add(-1, 'year')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      indexType: 'OverdueData',
      showDecisionChart: false,
      showVariationChart: false,
      showEnvChart: false
    };
  },
  watch: {
    indexType: function(newV) {
      this.getEnvData(newV);
    },
    rcProductName: function(newV) {
      this.getDecisionData();
    },
    moduleType: function(newV) {
      this.getDecisionData();
    }
  },
  created() {
    this.getMsgList();
    // 环境监控
    this.getEnvData(this.indexType);
  },
  mounted() {
    this.getDecisionData();
  },
  methods: {
    //获取消息列表
    getMsgList() {
      getMessageList()
        .then(result => {
          if (result.result && Array.isArray(result.result.items)) {
            this.msgListArr = result.result.items;
          }
        })
        .catch(err => console.log(err));
    },
    //点击查看
    lookup(item = {}) {
      if (!item.status) {
        item.status = 1;
        this.$store.dispatch('reduceUnReadNumber');

        updateMessageStatus(item.id)
          .then(result => {
            console.log(result);
          })
          .catch(err => console.log(err));
      }
      item.expansion = item.expansion || {};
      if (item.messageType === 1) {
        this.$router.push({
          name: 'univariate-monitor',
          query: {
            modulename: item.expansion.moduleName,
            productname: item.expansion.productName,
            moduletype: item.expansion.moduletype,
            variablekey: item.expansion.variableKey,
            transmittype: item.expansion.transmittype
          }
        });
      } else if (item.messageType === 2) {
        this.$router.push({
          name: 'decision-monitor',
          query: {
            modulename: item.expansion.moduleName,
            productname: item.expansion.productName,
            moduletype: item.expansion.moduletype
          }
        });
      } else {
        this.$router.push({
          name: 'module-view',
          params: {moduleid: item.productid},
          query: {
            reportid: item.productReportid,
            starttime: item.createTimeStart,
            endtime: item.createTimeEnd
          }
        });
      }
    },
    getEnvData(type) {
      if (!(this.envDates[0] && this.envDates[1])) {
        this.$message({
          message: '请选择查询时间',
          type: 'warning'
        });
        return;
      }
      const data = {
        startdate: this.envDates[0],
        enddate: this.envDates[1]
      };
      this.showEnvChart = true;
      this.axios({
        method: 'post',
        url: `https://openrmcloud.vcredit.com/Bbs/${type}`,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify(data)
      })
        .then(result => {
          this.drawEnvChart(result.data);
        })
        .catch(err => {
          console.log(err);
          this.showEnvChart = false;
        });
    },
    drawEnvChart(data) {
      if (!this.envChart) {
        this.envChart = echarts.init(document.getElementById('env-chart'));
      }
      const options = {
        title: {
          text: envObj[this.indexType],
          top: 20,
          left: 'center',
          textStyle: {
            fontSize: 16
          }
        },
        xAxis: {
          type: 'category',
          data: data.xAxisdata
        },
        grid: {
          left: '5%',
          right: '5%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line'
          }
        },
        yAxis: {
          scale: true,
          splitArea: {
            show: true
          },
          min: 0,
          boundaryGap: [0.2, 0.2]
        },
        series: [
          {
            name: data.seriesdata[0].name,
            data: data.seriesdata[0].data,
            type: 'line',
            smooth: true
          }
        ]
      };
      if (data.xAxisdata.length > 30) {
        options.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      this.envChart.setOption(options);
    },
    upDateEnvDate(val) {
      this.envDates = val;
      this.getEnvData(this.indexType);
    },
    getDecisionData() {
      this.getTotal();
      this.$refs.decisionChart.$emit('drawDecisionChart', this.rcProductName, Number(this.moduleType));
    },
    getTotal() {
      getProMonDataFromYday(this.rcProductName, Number(this.moduleType))
        .then(result => {
          for (let item of this.totalList) {
            item.value = result.result[item.type] || 0;
          }
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

