<template>
  <div class="univariate-result">
    <div class="return-page" v-if="sourceChannel === 'univariate-view'">
      <a href @click.prevent.stop="returnPrev">重新分析</a>
    </div>
    <div class="parm-des" v-if="sourceChannel === 'univariate-view'">
      <span>{{parmDes}}</span>
    </div>
    <el-card v-for="(one, i) in resultallList" :key="i">
      <div slot="header">
        <span>{{ getTitle(i) }} 数据统计图</span>
      </div>
      <div class="wrap-result-table">
        <div :id="`resultbar${i}`" class="resultbar" v-if="one.resultall.length && one.showbar"></div>
        <span class="nodata el-table__empty-block" v-if="!one.resultall.length || !one.showbar">暂无数据</span>
      </div>
    </el-card>

    <div v-if="sourceChannel === 'univariate-view'">
      <el-card v-for="(one, i) in resultallList" :key="i + resultallList.length">
        <div slot="header">
          <span>{{ getTitle(i) }} 数据统计结果</span>
        </div>
        <el-table :data="one.resultall" :class="`download-table-${i}`">
          <el-table-column type="index"></el-table-column>
          <el-table-column prop="key" label="变量取值" align="center"></el-table-column>
          <el-table-column prop="address" :label="`${getTitle(i)} 结果值 `" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.value===null ? "null" : scope.row.value}} {{showPercent(i,scope.row.value)}}</span>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>

    <div v-if="sourceChannel === 'univariate-view'" v-show="showsave" class="box-save-export">
      <el-button type="primary" @click.stop.prevent="saveResult">保存</el-button>
      <el-button type="primary" v-if="showsource" @click.stop.prevent="exportSource">导出数据源</el-button>
      <el-button type="primary" @click.stop.prevent="export2Excel">导出图表</el-button>
    </div>

    <filename
      v-if="sourceChannel === 'univariate-view'"
      @save="saveFile"
      :init-visible.sync="showfilesave"
    ></filename>
  </div>
</template>

<script>
import filename from '@/components/filename';
import mixinsUnivar from '@/mixins/mixins-univar.js';
import mixinsVar from '@/mixins/mixins-var.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryAnalysisDetail} from '@/api/analysis.js';
import {exportDecision} from '@/api/decision.js';
import {getOnePrefilter, getSectionContent, getOneVarSelect, getOverdueList} from '@/assets/scripts/getSomeSetting.js';
import {exportExcel, json2Excel} from '@/assets/scripts/table2excel.js';

export default {
  name: 'univariate-result',
  components: {
    filename
  },
  mixins: [mixinsVar, mixinsUnivar],
  props: {
    recordId: {
      type: String
    },
    sourceChannel: {
      type: String,
      required: true
      // default: 'univariate-view'
    }
  },
  data() {
    return {
      resultallList: [
        {
          showbar: false,
          postdata: {},
          resultall: []
        }
      ],
      decisionDates: [],
      loanDates: [],
      indicator: [],
      varList: [],
      overdue: [],
      prefilter: [getOnePrefilter()],
      varselectlist: [getOneVarSelect()],
      sectioncontent: getSectionContent(), // 变量分组方式可选的内容
      overduelist: getOverdueList(),
      showfilesave: false
    };
  },
  computed: {
    showsource() {
      // 是否显示导出数据源按钮
      return this.indicator.indexOf('AFTER_LOAN') > -1 && !this.recordId;
    },
    showsave() {
      return this.resultallList[0].resultall.length > 0;
    },
    parmDes() {
      let theOne = this.varList.find(item => item.value === this.varselectlist[0].fieldName) || {};
      let keyArr = this.resultallList.map((item, i) => {
        return this.getTitle(i);
      });
      return `${this.postdata.indexName} ${theOne.label} ${keyArr.join('\\')}`;
    }
  },
  created() {
    this.postdata = {};
    this.getAnalysisResult()
      .then(result => {
        if (Array.isArray(result)) {
          this.resultallList = result;
          this.postdata = this.initFields(result);
          this.getVars([this.rcProductName, this.moduleType, this.samplename], true, {}, true);
        }
      })
      .catch(err => console.log(err));
  },
  methods: {
    returnPrev() {
      this.$store.commit('UPDATE_VAR_TYPE', 'univariate');
      this.$store.commit('UPDATE_ANALYSISRESULT', {
        key: 'univariate',
        value: this.resultallList
      });
      if (this.filesaveid) {
        this.$store.commit('UPDATE_ANALYSISRESULT', {
          key: 'file',
          value: {
            name: this.filename,
            id: this.filesaveid
          }
        });
        this.$router.push({
          path: '/variate',
          query: {
            recordid: this.filesaveid
          }
        });
      } else {
        this.$router.push('/variate');
      }
    },
    getAnalysisResult() {
      return new Promise((resolve, reject) => {
        if (this.recordId) {
          // 获取保存的分析结果
          queryAnalysisDetail(this.recordId)
            .then(result => {
              if (result.result == null) {
                this.$message.warning('此历史记录不存在');
                reject('此历史记录不存在');
                return;
              }
              this.filename = result.result.name;
              this.filesaveid = result.result.id;
              const resultallList = JSON.parse(result.result.result);
              resolve(resultallList);
            })
            .catch(err => reject(err));
        } else {
          const resultallList = this.$store.state.analysisResult.univariate;
          const file = this.$store.state.analysisResult.file;
          this.filename = file.name;
          this.filesaveid = file.id;
          resolve(resultallList);
        }
      });
    },
    getVars(val, isMain = true, target, isInit = false) {
      target = isMain ? this : target;
      target.rcProductName = val[0];
      target.moduleType = val[1];
      target.samplename = val[2];
      target.initSample = val;
      getVarList(target.samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            target.varList = result.result.map(({key, vbname, fieldtype, transmittype}) => {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (isMain && isInit) {
              this.initFieldsAboutVar(this.postdata);
              this.$nextTick(() => {
                for (let i = 0; i < this.resultallList.length; i++) {
                  this.drawBar(this.resultallList[i].resultall, i);
                }
              });
            }
          }
        })
        .catch(err => console.log(err));
    },
    saveResult() {
      if (!this.filesaveid) {
        this.showfilesave = true;
        return;
      }
      this.saveFile(this.filename);
    },
    exportSource() {
      if (!this.checkSource()) {
        console.log('======导出数据源 表单检查不通过======');
        return;
      }
      const postdata = this.getPostData();
      const promiseArr = [];
      if (this.indicator.indexOf('BEFORE_LOAN') > -1) {
        let data = JSON.parse(JSON.stringify(postdata));
        // 选择贷前
        if (this.beforeway === 'count') {
          // 选择计数
          data.resultValueType = 'COUNT';
          data.resultField = '';
        }
        if (this.beforeway === 'avg') {
          // 选择平均值
          data.resultValueType = 'FIELD_AVG';
          data.resultField = this.avgvar;
        }
        data.loanType = 'BEFORE_LOAN';
        data.resultNeedPercent = this.ispercent;
        promiseArr.push(exportDecision(data));
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1) {
        for (let item of this.overdue) {
          let data = JSON.parse(JSON.stringify(postdata));
          let overduedata = item.split('-');
          data.afterLoanKPI = overduedata[0];
          data.overdueDays = overduedata[1];
          data.loanType = 'AFTER_LOAN';
          // 选择贷后
          if (this.aftertype === 'peoples') {
            // 类型选择人数
            data.resultValueType = 'COUNT';
            data.resultField = '';
          } else if (this.aftertype === 'amount') {
            // 类型选择金额
            data.resultValueType = 'FIELD_SUM';
            data.resultField = 'LoanCapital';
          }
          if (this.aftercount === 'count') {
            // 统计值选择逾期量
            data.resultNeedPercent = false;
          } else if (this.aftercount === 'rate') {
            // 统计值选择逾期率
            data.resultNeedPercent = true;
          }
          promiseArr.push(exportDecision(data));
        }
      }

      Promise.all(promiseArr)
        .then(results => {
          for (let i = 0; i < results.length; i++) {
            let data = results[i];
            if (data.result.length === 0) {
              return;
            }
            this.exportSource2Excel(data.result, i);
          }
        })
        .catch(err => console.log(err));
    },
    export2Excel() {
      for (let index = 0; index < this.resultallList.length; index++) {
        exportExcel(`.download-table-${index}`, `单变量分析_${this.getTitle(index)}_数据统计结果${Date.now()}.xlsx`);
      }
    },
    exportSource2Excel(body, i) {
      const bodydata = body.map(function(v1) {
        return v1.map(function(v2) {
          if (v2 === null) {
            v2 = 'N/A';
          }
          return v2;
        });
      });
      json2Excel(bodydata, `${this.samplename}_${this.getTitle(i)}_${new Date().toLocaleString()}.xlsx`);
      //   });
    },
    saveFile(name) {
      let data = {};
      let theOne = this.varList.find(item => item.value === this.varselectlist[0].fieldName) || {};
      let keyArr = this.resultallList.map((item, i) => {
        return this.getTitle(i);
      });
      let parmDes = `${this.postdata.indexName} ${theOne.label} ${keyArr.join('\\')}`;
      data.parmDes = parmDes;
      data.name = name;
      data.type = 0;
      data.id = this.filesaveid;
      const resultallList = JSON.parse(JSON.stringify(this.resultallList));
      for (let one of resultallList) {
        for (let item of one.postdata.statisticConditionList) {
          for (let assistVar of item.assistVarList) {
            delete assistVar.varList;
          }
        }
      }
      data.result = JSON.stringify(resultallList);
      saveAnalysis(data)
        .then(result => {
          this.$message.success('保存成功');
          this.filename = data.name;
          this.filesaveid = result.result;
        })
        .catch(err => console.log(err));
    },
    showPercent(i, value) {
      if (value == null) {
        return '（0%）';
      }
      let data = this.resultallList[i].postdata;
      if (this.resultallList[i].ispercent) {
        //贷前显示百分比
        let allCount = 0;
        for (let item of this.resultallList[i].resultall) {
          let v = item.value ? parseFloat(item.value) : 0;
          allCount += v;
        }
        let rate = allCount > 0 ? ((value / allCount) * 100).toFixed(2) : 0;
        return '（' + rate + '%）';
      } else if (data.resultNeedPercent) {
        //贷后显示逾期率
        return '（%）';
      } else {
        return '';
      }
    },
    checkSource() {
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1 && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }
      return true;
    }
  }
};
</script>

<style scoped>
.univariate-result {
  margin-bottom: 60px;
}
.el-card {
  margin: 20px 15px;
}
.resultbar {
  position: relative;
  left: 0;
  right: 0;
  width: 100%;
  height: 300px;
}
.nodata {
  color: #999;
}
</style>
