<template>
  <div class="decision-monitor-result">
    <el-card>
      <div slot="header">
        <span class="card-name">决策监控图</span>
      </div>
      <decision-monitor-chart ref="decisionChart"></decision-monitor-chart>
    </el-card>
  </div>
</template>

<script>
import decisionMonitorChart from '@/components/decision-monitor-chart';
import {queryAnalysisDetail} from '@/api/analysis.js';

export default {
  name: 'decision-monitor-result',
  components: {
    decisionMonitorChart
  },
  props: {
    recordId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      initSample: [],
      decisionDates: [],
      moduleName: '',
      varKey: '',
      transmittype: '1'
    };
  },
  created() {
    if (this.recordId) {
      this.getAnalysisResult();
    }
  },
  methods: {
    getAnalysisResult() {
      queryAnalysisDetail(this.recordId)
        .then(result => {
          if (result.result == null) {
            this.$message.warning('此历史记录不存在');
            return;
          }
          this.dealWithParams(result.result);
        })
        .catch(err => console.log(err));
    },
    dealWithParams(result) {
      this.filename = result.name;
      this.filesaveid = result.id;
      const params = JSON.parse(result.result);
      this.initSample = params.initSample;
      this.decisionDates = [params.startTime, params.endTime];
      this.moduleName = params.modulename;
      this.totalIV = params.totalIV;
      if (this.totalIV) {
        this.showTooltip = true;
      }
      this.tooltipStyle = params.tooltipStyle;
      this.params = params;

      this.$refs.decisionChart.$emit(
        'drawDecisionChart',
        this.initSample[0],
        this.moduleName,
        this.decisionDates[0],
        this.decisionDates[1]
      );
    }
  },
  beforeDestory() {
    this.$store.commit('UPDATE_MONITORPAGE', {
      key: 'decision',
      value: 'form'
    });
  }
};
</script>

<style scoped>
.decision-monitor-result {
  margin-bottom: 60px;
}
.el-card {
  position: relative;
  margin: 20px 15px;
}
</style>
