<template>
  <div class="record-container">
    <div class="record-params">
      <div class="cell">文件名称</div>
      <div class="cell">分析类型</div>
      <div class="cell">参数说明</div>
      <div class="cell">操作</div>
    </div>
    <div class="record-list" v-for="(item, i) in recordList" :key="i">
      <div class="record-params">
        <div class="cell">{{item.name}}</div>
        <div class="cell">{{formatter(item)}}</div>
        <div class="cell">{{item.parmDes}}</div>
        <div
          class="cell"
          @click.stop.prevent="toggleRecordDetailFunc(item)"
        >{{item.showResult ? '收起' : '查看'}}</div>
      </div>
      <div class="record-result" v-if="item.showResult">
        <univariate-result
          class="container"
          v-if="item.type === 0"
          :record-id="item.id"
          source-channel="record-container"
        ></univariate-result>
        <multivariate-result
          class="container"
          v-if="item.type === 1"
          :record-id="item.id"
          source-channel="record-container"
        ></multivariate-result>
        <univariate-monitor-result
          class="container"
          v-if="item.type === 2"
          :record-id="item.id"
          source-channel="record-container"
        ></univariate-monitor-result>
        <decision-monitor-result
          class="container"
          v-if="item.type === 3"
          :record-id="item.id"
          source-channel="record-container"
        ></decision-monitor-result>
      </div>
    </div>
  </div>
</template>

<script>
import univariateResult from '@/components/univariate-result';
import multivariateResult from '@/components/multivariate-result';
import univariateMonitorResult from '@/components/univariate-monitor-result';
import decisionMonitorResult from '@/components/decision-monitor-result';

import {getAnalysisList} from '@/api/analysis.js';

export default {
  name: 'analysis-record',
  components: {
    univariateResult,
    multivariateResult,
    univariateMonitorResult,
    decisionMonitorResult
  },
  data() {
    return {
      recordList: []
    };
  },
  created() {
    getAnalysisList()
      .then(result => {
        const data = result.result;
        if (Array.isArray(data.items)) {
          this.recordList = data.items;
          for (const item of this.recordList) {
            this.$set(item, 'showResult', true);
          }
          return;
        }
        this.recordList = [];
      })
      .catch(err => {
        console.log(err);
        this.recordList = [];
      });
  },
  methods: {
    formatter(row) {
      return this.$options.filters.analyseType(row.type);
    },
    toggleRecordDetailFunc(item) {
      item.showResult = !item.showResult;
    }
  }
};
</script>

<style scoped>
.record-container {
  width: 100%;
  /* flex-direction: column; */
}
.record-list .record-params {
  color: #606266;
}
.record-params {
  display: flex;
  height: 34px;
  align-items: center;
  border-bottom: 1px solid #ebeef5;
  color: #409eff;
}
.record-params .cell:nth-child(1),
.record-params .cell:nth-child(3) {
  width: calc(50% - 110px);
  text-align: center;
}
.record-params .cell:nth-child(2) {
  width: 140px;
  text-align: center;
}
.record-params .cell:last-child {
  width: 60px;
  text-align: center;
  color: #409eff;
  cursor: pointer;
}
</style>
